import uniIcon from "@/components/uni-icons/uni-icons.vue";
const JsyServer = require("services/jsy-server.js");
export default {
	components:{
		uniIcon
	},
	data() {
		return {
			phone: '',
			password: '',
			passType: 'password',
			isPhoneClear: false,
			isPassClear: false,
			checkProtocol: true,
			countdown:'获取验证码',
			isDisplay: true,
			disabled:false,
			timestatus:false,
			clear:'',
			icon_color:'#FF6000'
		};
		
		},
	methods:{
		backto:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		showPass: function(){
			this.passType = this.passType==='password'?'text':'password'
		},
		clearPhone: function(){
			this.phone = ''
		},
		clearPass: function(){
			this.password =''
		},
		showCloseIcon:function(e){
			if (e.target.value){
				this.isPhoneClear = true
			}else {
				this.isPhoneClear = false
			}
		},
		showPassClearIcon: function(e){
			if(e.target.value) {
				this.isPassClear = true
			}else {
				this.isPassClear = false
			}
			
		},
		examineCount:function(){
			
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phone))){
				uni.showToast({
					title: '请输入正确电话号码！',
					icon: 'none',
					duration: 2000
				});
				return ;
		    }
		},
		agreeProtocol: function(){
			if (this.checkProtocol){
				this.icon_color = '#FF6000'
			}else{
				this.icon_color = '#cccccc'
			}
			this.checkProtocol = !this.checkProtocol
			
		},
		getValidCode:function(){
						var that = this;
						if (!that.phone){
							uni.showToast({
								title: '请输入手机号码',
								icon: 'none'
							});
							return;
						}
						
						that.disabled = true
						let _data ={
							phone: that.phone
						}
						uni.showToast({
							title: '验证码已发送',
											icon: 'none'
						});
						that.timestatus = true
						
						JsyServer.verification(_data).then(res => {
						  that.countdown = 60
						  that.clear = setInterval(that.countDown,1000)
						  console.log(res);
						  if(res.data.status == 0){
							 
							  
						  }else{
							  that.disabled = false
						  }
						 
						  
							
						}).catch(err => {
						  wx.showToast({
						    title: err.data.errMsg,
						    icon: 'none'
						  });
						});		
					},
					countDown(){
		                var that = this;
		                if(!that.countdown){                    
		                    that.disabled = false;
		                    that.timestatus = false;
		                    that.countdown = '获取验证码';
		                    clearInterval(that.clear);
		                }else{
		                    --that.countdown;
		                }
		            },
		formSubmit: function(e){
			let data = e.detail.value;
			let code = data.code;
			delete data.code;
			console.log(data);
			
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(data.phone))){
				uni.showToast({
					title: '请输入正确电话号码！',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			if(!data.password || data.password.length<6){
				uni.showToast({
					title: '请输入至少六位密码',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			if(!code){
				uni.showToast({
					title: '验证码不能为空',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			if (!this.checkProtocol){
				uni.showToast({
					title: '请同意轻纺车网协议服务',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			uni.request({
				url: this.Api.registration,
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: { 
					phone: data.phone,
					password: data.password,
					verification: code
				},
				success: res => {
					console.log(res.data);
					if (res.data.status  == 0){
						uni.switchTab({
							url: "/pages/qing-f-c/index"
						})
					}
				},
				fail: (err) => {
					console.log(err.data)
				},
				complete: () => {}
			});
			
		}
	
	}
}
